/* 공통 스타일 */
.bizz0, .bizz60, .bizz120, .bizz180, .bizz240, .bizz300 {
  position: absolute;
  transform: translate(-50%, -50%);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #9B9B9B;
  z-index: 100;

  display: flex;
  align-items: center;
  justify-content: center;
  
 
}

.bizz-text {
    position: absolute;
  /* right 대신 transform 사용 */
  transform: translateX(-55px); /* 비즈 위로 45px 띄우기 */
  
  white-space: nowrap;
  color: white;
  padding: 4px 10px;
  border-radius: 20px; /* 좀 더 부드러운 캡슐 느낌 */
  font-size: 13px;
  pointer-events: none;
  transition: all 0.3s ease; /* 부드러운 움직임 */
}

/* 0도 자리 강조 */
.special-bizz {
  z-index: 999;
  background-color: white !important;
  box-shadow: 0px 0px 25px 10px rgba(255, 255, 255, 0.3);
}

/* 좌표 (750,750)을 중심으로 반지름 750px 기준 */
.bizz0 {
  left: calc(750px + 750px);
  top: 750px;
}

.bizz60 {
  left: calc(750px + 750px * 0.5);
  top: calc(750px - 750px * 0.866); /* cos60=0.5, sin60=0.866 */
}

.bizz120 {
  left: calc(750px - 750px * 0.5);
  top: calc(750px - 750px * 0.866);
}

.bizz180 {
  left: 0px;
  top: 750px;
}

.bizz240 {
  left: calc(750px - 750px * 0.5);
  top: calc(750px + 750px * 0.866);
}

.bizz300 {
  left: calc(750px + 750px * 0.5);
  top: calc(750px + 750px * 0.866);
}


